main {


    section {
        min-height: 100dvh;
    }

    section#technologies {
        background: #ddd;

        max-width: 100vw;

        .tech {
            padding: 1rem;

            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            gap: 2rem;

            .language-box {
                background-color: white;
                border: 1px solid #ddd;
                border-radius: 8px;
                padding: 2rem 1.5rem;
                text-align: center;
                box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
                transition: transform 0.3s ease, box-shadow 0.3s ease;

                text-decoration: none;
                cursor: pointer;

                &:hover {
                    transform: translateY(-5px);
                    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
                }

                img {
                    width: 100px;
                    object-fit: cover;
                    margin-bottom: 10px;
                }

                h3 {
                    margin: 0;
                    margin-top: 1rem;
                    font-size: 18px;
                }

            }
        }

    }

    section#resources {
        padding: 1rem;

        h1 {
            display: flex;
            gap: .5rem;
        }


        ul {
            list-style: none;

            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
            gap: 2rem;


            li {
                background: rgb(233, 237, 237);
                padding: 1rem;
                border-radius: 10px;
                transition: .2s all;

                box-shadow: 2px 2px 7px #313f626b;


                &:hover {
                    scale: 1.05;
                    background: rgb(193, 229, 255);
                }

                a {
                    text-decoration: none;
                    color: #000;
                    display: flex;
                    flex-direction: column;
                    gap: 1rem;

                    h3 {
                        margin: 0;
                        color: #8f8f8f;
                        font-size: 1rem;
                    }

                    img.thumbnail {
                        width: 100%;
                        border-radius: 10px;
                    }


                    .author_info {

                        display: flex;
                        gap: .5rem;
                        align-items: center;

                        img {
                            border-radius: 50%;
                            width: 30px;
                            height: 30px;
                        }

                    }

                }
            }
        }
    }
}



/* General Styles */
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

h1,
h2,
h3 {
    margin: 10px 0;
}

/* Hero Section */
.hero {
    position: relative;
    top: 0;
    text-align: center;
    background: radial-gradient(#2575fc, #7b00ff);
    color: #fff;
    padding: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    max-height: 100dvh;
    z-index: 1;

    h1 {
        font-size: 2.5rem;
        margin-bottom: 10px;
    }

    p {
        font-size: 1.2rem;
        margin-bottom: 20px;
    }

    .cta-button {
        background: #fff;
        color: #2575fc;
        padding: 10px 20px;
        border-radius: 5px;
        font-size: 1rem;
        text-decoration: none;
    }
}

/* Categories Section */
.categories-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
}

.categories-list li {
    list-style: none;
    text-align: center;
}

.categories-list img {
    width: 100%;
    height: auto;
    border-radius: 10px;
    margin-bottom: 10px;
}

/* Featured Section */
.featured-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
}

.featured-list li {
    list-style: none;
    text-align: center;
    background: #f9f9f9;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.featured-list img {
    width: 100%;
    height: auto;
    border-radius: 10px;
    margin-bottom: 10px;
}